	import OrderItem from "@/components/order-item.vue";

	export default {
		components: {
			OrderItem
		},
		data() {
			return {
				tabList: [{
						name: "全部",
						choose: true,
						type: ""
					},
					{
						name: "处理中",
						choose: false,
						type: "1"
					},
					{
						name: "已完成",
						choose: false,
						type: "3"
					},
					{
						name: "已取消",
						choose: false,
						type: "2"
					},
					{
						name: "已拒绝",
						choose: false,
						type: "4"
					}
				],
				dataList: [],
				orderStatus: [],
				orderType: "",
				title:'全部',
				nodata: false,
				nomore: false,
				pageIndex: 1,
				pagesize: 10,
				pageTotal: 1
			};
		},
		onShow() {
			this.getStatus()
		},
		onPullDownRefresh() {
			this.setParams()
			this.getList();
			wx.stopPullDownRefresh()
		},
		onReachBottom() {

			if (this.pageIndex < this.pageTotal) {
				this.pageIndex++
				this.getList();
			} else {

				this.nomore = true
				return false;
			}
		},
		methods: {
			getStatus() {
				let self = this;
				this.$acFrame.HttpService.refundStatus().then(res => {
					if (res.success) {
						console.log(res.data);
						var tabs = res.data;
						var st = {};

						tabs.filter(function(item) {
							self.orderStatus[item.key] = item.val;
						});
						self.getList();

					}
				})
			},
			getList() {
				let self = this
				let params = {
					status: this.orderType,
					pageSize: self.pagesize,
					pageIndex: self.pageIndex,
				}
				self.$acFrame.HttpService.refundList(params).then(res => {
					console.log(res);
					if (res.success) {
						let list = res.data.rows
						self.pageTotal = res.data.pageTotal
						if (list.length > 0) {
							list.forEach(function(item) {
								item["statusName"] = self.orderStatus[item["status"]];
								item.detailList.filter(v => {
									v.goodsImgPath = self.$acFrame.Util.setImgUrl(v.goodsImgPath);
								})
							});
							if (self.pageIndex == 1) {
								self.dataList = list;
							} else {
								self.dataList = self.dataList.concat(list)
							}

						} else {
							self.is_over = true;
							self.nodata = true
						}
					} else {

					}
				})
			},
			goDetail(item){
				let title = ''
				uni.navigateTo({
					url:"procesDetail?id="+item.id+'&title='+title
				})
			},
			tabClick(ind) {
				this.tabList.filter((v,i)=>{
					if(i==ind){
						v.choose=true
						this.orderType = v.type?v.type * 1:'';
						this.title = v.name
					}else{
						v.choose=false
					}
				})
				this.setParams()
				this.getList();
			},
			cancelRefund(id){
				let self = this
				this.$acFrame.HttpService.cancelRefund({id:id}).then(res => {
					if (res.success) {
				self.$acFrame.Util.mytotal('取消售后成功！')
				setTimeout(()=>{

					self.getList()
				},1000)
					}
				})
			},
			refundAgain(item){
        		let obj={
					orderId: item.orderId,
					orderDetailId: item.detailList[0]?item.detailList[0].id:'',
					price: item.moneyAsk,
					askNum: item.detailList[0]?item.detailList[0].buyNum:'',
					phone: item.address ? item.address.receiverMobilePhone : ''
        		}
        		let type = item.type == 2 ? 'hasgoods':'nullgoods'
				uni.navigateTo({
					url:'/pages/order/returnForm?orderData=' + JSON.stringify(obj) + '&type='+type
				})
    		},
			setParams(){
				this.nomore=false
				this.nodata=false
				this.pageIndex = 1;
				this.dataList = [];
			},
			setImg(src) {
				return this.$acFrame.Util.setImgUrl(src);
			},
		}
	};
